.left-magic-card-appear {
  transform: translateX(-40vw);
}

.left-magic-card-appear.left-magic-card-appear-active {
  transform: translateX(0vw);
  transition: all .7s ease;
}

.right-magic-card-appear {
  transform: translateX(40vw);
}

.right-magic-card-appear.right-magic-card-appear-active {
  transform: translateX(0vw);
  transition: all .7s ease;
}

.announces-appear {
  transform: translateY(-65vh);
}

.announces-appear.announces-appear-active {
  transform: translateY(0);
  transition:transform .8s ease;
}

.left-magic-card-leave {
  opacity: 1;
}
