@import url('https://fonts.googleapis.com/css?family=Lato|Playfair+Display:400i&display=swap&subset=cyrillic');
@import url('https://fonts.googleapis.com/css?family=Old+Standard+TT:400,400i&display=swap&subset=cyrillic-ext');
@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:100,200,300,400&display=swap&subset=cyrillic-ext');
@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Sans:100,100i,200,200i,400&display=swap&subset=cyrillic-ext');
@import url('https://cdn.materialdesignicons.com/4.7.95/css/materialdesignicons.min.css');

::-webkit-scrollbar {
    width: 3px;
}

::-webkit-scrollbar-thumb {
    background: #212121;
}

html {
    height: 100%;
}

body {
    /*TODO: This may cause unexpected problems*/
    overflow-x: hidden;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
